import { Controller } from '@ali/4ever-cangjie';
import { Marks } from '@ali/4ever-mo';



export default function getLetterSpacing(controller: Controller) {
  const { value } = controller;
  // 不支持列表符号
  if (value.data.listSelections && value.data.listSelections.length > 0) return 0;

  // 公共字符间距
  const activeSpacingMark = controller.query('getActiveMark', Marks.Spacing.TYPE);

  if (!activeSpacingMark) return 0;

  return activeSpacingMark.data.value || 0;
}
